import React, {useCallback, useEffect, useState} from 'react';
import ClayForm, {ClayInput, ClaySelect, ClaySelectBox} from '@clayui/form';
import ClayButton from '@clayui/button';
import {Heading} from '@clayui/core';

import { getSite } from './site';


function BlogPostings1() {
  return (
    <div style={{width:'100%'}}>
      <h1>Blog Posts</h1>
      <hr />
      <div>
        <BlogPosting />
      </div>
    </div>
  );
}


function BlogPosting() {
  const [site, setSite] = useState('');
  const [headline, setHeadline] = useState('');
  const [articleBody, setArticleBody] = useState('');
  const [blogId, setBlogId] = useState('')

  useEffect(() => {
	getSite().then(res => {
      setSite(res.items[0].id);
	})
  }, []);

  const postBlog = useCallback(() => {
    Liferay.Util.fetch(`/o/headless-delivery/v1.0/sites/${site}/blog-postings`, {
      method: 'POST',
      headers: {'Content-Type':'application/json'},
      body: JSON.stringify({'headline':headline, 'articleBody':articleBody})
    }).then(() => {
        setHeadline('');
        setArticleBody('');
    });
  },
  [
    headline,
    articleBody
  ]);

  const putBlog = useCallback(() => {
    Liferay.Util.fetch(`/o/headless-delivery/v1.0/blog-postings/${blogId}`, {
      method: 'PUT',
      headers: {'Content-Type':'application/json'},
      body: JSON.stringify({'headline':headline, 'articleBody':articleBody})
    }).then(() => {
        setHeadline('');
        setArticleBody('');
    });
  },
  [
    blogId,
    headline,
    articleBody
  ]);

  const deleteBlog = useCallback(() => {
    Liferay.Util.fetch(`/o/headless-delivery/v1.0/blog-postings/${blogId}`, {method:'DELETE'});
  },[blogId]);

  return (
    <div>
      <ClayForm.Group>
        <label htmlFor='blogId'>Blog ID</label>
        <ClayInput
          id="blogId"
          onChange={event => setBlogId(event.target.value)}
          type="text"
          value={blogId}
        />
      </ClayForm.Group>
      <ClayButton displayType="primary" onClick={() => deleteBlog()}>
        Delete Blog
      </ClayButton>
      <br style={{marginBottom:"4em"}}/>
      <ClayForm.Group>
        <label htmlFor='headline'>Headline</label>
        <ClayInput
          id="headline"
          onChange={event => setHeadline(event.target.value)}
          type="text"
          value={headline}
        />
      </ClayForm.Group>
      <ClayForm.Group>
        <label htmlFor='articleBody'>Article Body</label>
        <ClayInput
          id="articleBody"
          component="textarea"
          onChange={event => setArticleBody(event.target.value)}
          type="text"
          value={articleBody}
        />
      </ClayForm.Group>
      <ClayButton displayType="primary" onClick={() => postBlog()}>
        Post New Blog
      </ClayButton>
      <ClayButton displayType="secondary" onClick={() => putBlog()}>
        Update Blog
      </ClayButton>
    </div>
  );
}


export default BlogPostings1;